.ProductDetails {
    padding: 80px 10px 10px 20px;
    width: 100%;
    min-height: 100vh;
    background: rgb(247, 246, 246);
    display: flex;
}

.ProductDetails-left {
    width: 40%;
    display: flex;
}

.otherImgs {
    display: flex;
    flex-direction: column;
}

.proimg {
    width: 50px;
    height: 60px;
    object-fit: contain;
    margin: 10px;
    border: 1px solid rgb(226, 222, 222);
    padding: 5px;
    cursor: pointer;
}

.proimg:hover {
    border: 2px solid rgb(107, 240, 66);
}

.ProductDetail-Img {
    width: 70%;
    height: 79vh;
    border: 1px solid rgb(221, 218, 218);
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 10px 10px;
}

.ProductDetail-Img>img {
    width: 195px;
    height: 416px;
    object-fit: contain;
}

.ProductDetail-btns {
    width: 100%;
}

.ProductDetail-btn {

    width: 130px;
    padding: 10px;
    font-size: 10px;
    margin: 1rem 4px;
    margin-top: -20px;
    border: none;
    outline: none;
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    border-radius: 2px;
    cursor: pointer;
}

.cart-btn {
    margin-left: 30px;
}

.ProductDetail-btn:nth-child(1) {
    background: #ff9f00;
}

.ProductDetail-btn:nth-child(2) {
    background: #fb641b;
}

.ProductDetails-right {
    width: 60%;
    padding: 10px 20px;
    /* overflow: scroll; */
}

.ProductName {
    font-size: 18px;
    color: #212121;
    letter-spacing: 1px;
}

.ratingsreviews {
    padding: 10px 0;
    display: flex;
    align-items: center;
}

.price {
    padding: 8px 0 0;
    font-size: 28px;
}

.packfee {
    font-size: 14px;
    color: #212121;
    font-weight: 300;
    padding: 5px 0;
}

.availableoff {
    font-size: 16px;
    padding: 10px 0;
}

.offers {
    padding: 5px 5px;
    list-style: none;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 300;
}

.offers>img {
    margin-right: 5px;
}

.offers b {
    margin-right: 10px;
}

.descriptions {
    padding: 20px 0;
}

.colors {
    display: flex;
    /* justify-content: center; */
}

.color {
    color: #a7a5a5;
}

.colorimgs {
    padding: 0 80px;
}

.colorimgs>img {
    width: 60px;
    height: 70px;
    object-fit: contain;
    margin: 0 0 8px 16px;
    border: 2px solid #ece9e9;
    padding: 5px;
    cursor: pointer;
}

.colorimgs>img:hover {
    border: 2px solid #1435f0;
}

.highlights {
    display: flex;
    padding: 20px 0;
}

.highlight {
    padding: 0 50px;
}

.highlight-title {
    color: #a7a5a5;
}

.highlight-item {
    padding: 0 0 8px 16px;
    font-size: 15px;
    font-weight: 300;
}

.RatingsAndReviews-title {
    font-size: 25px;
}

.RatingsAndReviews-container {
    display: flex;
    padding: 20px 0;
}

.RatingsAndReviews-stars {
    text-align: center;
    width: 150px;
    padding: 0 40px 10px 0px;
    margin-top: 1rem;
}

.RatingsAndReviews-reviews {
    font-size: 25px;
}

.RatingsAndReviews-ratings {
    font-size: 14px;
    color: #2b2a2a;
    font-weight: 300;
    padding: 5px 0;
}

.RatingsAndReviews-charts>li {
    list-style-type: none;
    padding: 5px 0;
    display: flex;
    align-items: center;
}

.star {
    padding: 0px 10px;
}

.bar {
    width: 300px;
    height: 5px;
    background: #cccaca;
    border-radius: 10px;
}

.innerbar {
    background: rgb(3, 112, 8);
    /* width: 50%; */
    height: 100%;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}